<template>
  <v-container style="max-width: 800px;">
    <v-timeline dense clipped>
      <v-timeline-item fill-dot class="white--text mb-5" color="orange" large>
        <v-icon slot="icon">mdi-gesture-tap</v-icon>
        <v-btn>new backup job</v-btn>
        <v-btn>new repository</v-btn>
      </v-timeline-item>
      <v-slide-x-transition group>
        <template v-for="event in timeline">
          <v-timeline-item
            :key="event.id"
            class="mb-3"
            :color="event.color || 'primary'"
            small
            :hide-dot="!!event.heading"
          >
            <span v-if="!!event.heading">{{event.heading.toUpperCase()}}</span>
            <v-layout justify-space-between v-else>
              <v-flex xs7>
                <v-chip
                  v-if="event.project"
                  class="white--text ml-0"
                  color="purple"
                  label=""
                  small
                >{{event.project.name}}</v-chip>
                {{event.text}}
              </v-flex>
              <v-flex xs5 text-xs-right v-text="event.time"></v-flex>
            </v-layout>
          </v-timeline-item>
          <v-timeline-item v-if="event.email" :key="event.id+'_'" class="mb-3" hide-dot>
            <v-btn class="mx-0">Resend Email</v-btn>
          </v-timeline-item>
        </template>
      </v-slide-x-transition>
    </v-timeline>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    events: [
      {
        id: 543456545,
        text: "repository created",
        time: "20:05",
        project: {
          name: "repo 1"
        },
        color: "success"
      },
      {
        id: 54345645,
        text: "backup job created",
        time: "20:08",
        project: {
          name: "weekly backup job"
        },
        color: "success"
      },
      {
        id: 456546,
        heading: "2 days ago"
      },
      {
        id: 54345,
        text: "backup job created",
        time: "12:08",
        project: {
          name: "backup job 1"
        },
        color: "success"
      },
      {
        id: 4593485793,
        text: "backup job completed",
        time: "10:08",
        project: {
          name: "backup job 1"
        },
        email: true,
        color: "success"
      },
      {
        id: 45893475,
        heading: "yesterday"
      },
      {
        id: 454593485793,
        text: "backup job failed",
        time: "10:08",
        project: {
          name: "weekly backup job"
        },
        email: true,
        error: true,
        color: "error"
      }
    ],
    input: null,
    nonce: 0
  }),

  computed: {
    timeline() {
      return this.events.slice().reverse();
    }
  },

  methods: {
    comment() {
      const time = new Date().toTimeString();
      this.events.push({
        id: this.nonce++,
        text: this.input,
        time: time.replace(/:\d{2}\sGMT-\d{4}\s\((.*)\)/, (match, contents) => {
          return ` ${contents
            .split(" ")
            .map(v => v.charAt(0))
            .join("")}`;
        })
      });

      this.input = null;
    }
  }
};
</script>